import React from 'react'
import fbicon from '../../../img/fb-icon.png'
import twittericon from '../../../img/twitter-icon.png'

const KnowUs = ()=> {
  return(
    <section className="know-us pb-5">
      <div className="container">
        <div className="row">
          <div className="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12">
            <h3 className="text-center mt-3 mb-2"></h3>
            <p className="text text-center mb-4 mt-4"> <a className="text-red" href="/faq"></a>  <a className="text-red" href="/contacts"></a> <br /> </p>
          </div>
          <div className="social-btn bottom-spacing-lg mx-auto mb-5">
            <a target="_blank"  href="https://www.facebook.com/5xruby">
            <img 
            src={fbicon} 
            className="mr-3" 
            alt="Icon fb" />
            </a>
            <a target="_blank"  href="https://twitter.com/5xruby">
            <img 
            src={twittericon} 
            alt="Icon twitter" />
            </a>
          </div>
        </div>
      </div>
    </section>
  )
}

export default KnowUs


